.secondSection {
    background-color: #0a0e0e;
    padding-top: 72px;
}

.description {
    font-size: 24px;
    line-height: 28px;
    font-weight: 400;
    color: #CAC9C6;
}

.contentWrap {
    margin: 0 auto;
    max-width: 1270px;
}

.secondSectionContent {
    margin-top: 150px;
    margin-bottom: 100px;
}

.secondSectionPhotoWrap {
    position: relative;
    z-index: 1;
}

.secondSectionPhoto {
    margin: 60px 0 0 50px;
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 536px;
}

.secondSectionPhotoWrap:before {
    content: '';
    border: 3px solid #FFE600;
    width: 90%;
    height: 90%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}